

.navbar {
    /* background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%); */
    align-items: center;
    /* border-bottom: 3px solid #f2f2f2; */
    height: 100px;
    box-sizing: border-box;
border-radius: 0px 0px 45px 45px;
    width: 100%;
    margin:auto;
    position: relative;
    margin-bottom: 100px;;
   }
  


  .navbar-container {
    display: flex;
    border-radius: 0px 0px 45px 45px;

  }

  .navbar-logo {

    display:flex;
    font-family: 'Circular Std Bold';

    position: absolute;
    color: rgb(0, 0, 0);
    padding-top: 16px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    width:400px;
    top:20px;
  }
  
  
  side-tag{
    font-weight: 300;
    font-style: normal;
    font-size: 12px;
    line-height: 2em;
    letter-spacing: .03em;
    text-transform: none;
    color: #aaa;
    padding-left: 10px;
    text-align:center;
 


  }

  .fa-typo3 {
    margin-left: 0.1rem;
    font-size: 1.8 rem;
    margin-top: 50px;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 95vw;
    justify-content: end;
    align-content: center;
  padding-top: 0px;

  }
  
  .nav-item {
    height: 100%;
    padding-top: 20px;
    padding-right:25px;
    

  }
  
  .nav-links {
    font-family: 'Circular Std Bold';

    color: rgb(0, 0, 0);
    display: flex;
    align-items: left;
    text-decoration: none;
    padding: 0.5rem;
    top:100px;
    font-size: 16px;


  }
  
  .nav-links:hover {
    border-bottom: px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }


